import React, { useEffect, useState } from "react";
import Typography from "@mui/material/Typography";
import Grid from "@mui/material/Grid";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import ThermostatIcon from "@mui/icons-material/Thermostat";
import { Database, ref, child, get, onValue } from "firebase/database";
import LinearProgress from "@mui/material/LinearProgress";

function Current(props: { database: Database }) {
  const dbRef = ref(props.database);
  const [actual, setActual] = useState<{ [key: string]: number }>({});
  const [mapping, setMapping] = useState<{ [key: string]: string }>({});
  const [requestedDate, setRequestedDate] = useState<Date>();

  const readData = () => {
    get(child(dbRef, "mapping")).then((snapshot) => {
      if (snapshot.exists()) {
        setMapping(snapshot.val());
      }
      get(child(dbRef, "actual")).then((snapshot) => {
        if (snapshot.exists()) {
          setActual(snapshot.val());
        }
      });
    });
  };

  useEffect(() => {
    readData();
    onValue(child(dbRef, "actual"), (snapshot) => {
      setActual(snapshot.val());
      setRequestedDate(new Date());
    });
  }, []);

  return (
    <Grid
      container
      spacing={2}
      direction="column"
      alignItems="center"
      justifyContent="center"
      padding={2}
    >
      <Grid item xs={12}>
        <Typography variant="h4">Aktulis</Typography>
      </Grid>
      <Grid item>
        <Grid container spacing={2} justifyContent="center">
          {Object.entries(actual).map(([key, value]) => (
            <Grid item key={key}>
              <Card sx={{ boxShadow: 3 }}>
                <CardContent>
                  {mapping[key] ? (
                    <Typography variant="h5">{mapping[key]}</Typography>
                  ) : (
                    <Typography variant="h5">{key}</Typography>
                  )}
                  <Typography variant="h4" display="inline">
                    {`${(Math.round(value * 100) / 100).toFixed(2)} `}
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
          ))}
        </Grid>
      </Grid>
      <Grid item>
        <LinearDeterminate modifiedDate={requestedDate} />
        <Typography>
          {requestedDate?.toDateString() + " " + requestedDate?.toLocaleTimeString()}
        </Typography>
      </Grid>
    </Grid>
  );
}

function LinearDeterminate(props: { modifiedDate?: Date }) {
  const [progress, setProgress] = React.useState(100);

  React.useEffect(() => {
    const timer = setInterval(() => {
      setProgress((oldProgress) => {
        const diff = 1;
        return Math.min(oldProgress - diff, 100);
      });
    }, 500);

    return () => {
      clearInterval(timer);
    };
  }, []);

  React.useEffect(() => {
    setProgress(100);
  }, [props.modifiedDate]);

  return <LinearProgress variant="determinate" color="success" value={progress} />;
}

export default Current;
