import React, { useEffect, useState } from "react";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
import Grid from "@mui/material/Grid";
import TextField from "@mui/material/TextField";
import { ref, set, child, get, Database } from "firebase/database";

function Naming(props: { database: Database }) {
  const dbRef = ref(props.database);
  const [actual, setActual] = useState<{ [key: string]: number }>({});
  const [mapping, setMapping] = useState<{ [key: string]: string }>({});

  const readData = () => {
    get(child(dbRef, "mapping")).then((snapshot) => {
      if (snapshot.exists()) {
        setMapping(snapshot.val());
      }
      get(child(dbRef, "actual")).then((snapshot) => {
        if (snapshot.exists()) {
          setActual(snapshot.val());
        }
      });
    });
  };

  useEffect(() => {
    readData();
  }, []);

  const sendData = () => {
    set(ref(props.database, "mapping"), mapping).then(() => readData());
  };

  return (
    <Grid container spacing={2} direction="column" alignItems="center" justifyContent="center">
      <Grid item>
        <Typography variant="h4">Elnevezsek</Typography>
      </Grid>
      {Object.keys(actual).map((key) => (
        <Grid item key={key}>
          <TextField
            label={key}
            variant="outlined"
            defaultValue={mapping[key] ?? ""}
            onChange={(e) =>
              setMapping((previous) => {
                previous[key] = e.target.value;
                return previous;
              })
            }
          />
        </Grid>
      ))}
      <Grid item>
        <Button onClick={sendData} variant="outlined" color="warning">
          Elnevezsek mdostsa
        </Button>
      </Grid>
    </Grid>
  );
}

export default Naming;
