import React, { useEffect, useState } from "react";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
import Grid from "@mui/material/Grid";
import TextField from "@mui/material/TextField";
import {
  Auth,
  signInWithEmailAndPassword,
  signInWithPopup,
  GoogleAuthProvider,
} from "firebase/auth";

const provider = new GoogleAuthProvider();
provider.addScope("https://www.googleapis.com/auth/contacts.readonly");

function Login(props: { auth: Auth; successfulCallback: () => void }) {
  const [email, setEmail] = useState<string>();
  const [password, setPassword] = useState<string>();

  const logInWithEmailAndPassword = () => {
    email &&
      password &&
      signInWithEmailAndPassword(props.auth, email, password).then(() => {
        setEmail("");
        setPassword("");
        props.successfulCallback();
      });
  };

  const logInWithGoogle = () => {
    signInWithPopup(props.auth, provider).then(() => {
      props.successfulCallback();
      console.log("Successful google login!");
    });
  };

  return (
    <Grid container spacing={2} direction="column" alignItems="center" justifyContent="center">
      <Grid item>
        <TextField label="email" variant="outlined" onChange={(e) => setEmail(e.target.value)} />
      </Grid>
      <Grid item>
        <TextField
          label="password"
          variant="outlined"
          onChange={(e) => setPassword(e.target.value)}
        />
      </Grid>
      <Grid item xs={12}>
        <Button
          onClick={logInWithEmailAndPassword}
          variant="outlined"
          disabled={!email || !password}
        >
          Login
        </Button>
      </Grid>
      <Grid item xs={12}>
        <Button onClick={logInWithGoogle} variant="outlined" color="error">
          Google login
        </Button>
      </Grid>
    </Grid>
  );
}

export default Login;
